import React, { useState, useEffect } from "react";
import Notification from "./Notification";
import LoginForm from "../Components/LoginForm";
import Togglable from "../Components/Togglable";
import phoneService from "../Services/entry_service";
import loginService from "../Services/login_service";

const PhonebookApp = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [newFilter, setNewFilter] = useState("");
  const [message, setMessage] = useState("");
  const [auth, setAuth] = useState(false);

  useEffect(async () => {
    try {
      const response = await loginService.checkStatus(
        window.localStorage.getItem("token")
      );
      setAuth(response);
    } catch (error) {
      console.error(error);
    }
  }, []);

  const getPersons = async () => {
    try {
      const entries = await phoneService.getAll();
      setPersons(entries);
    } catch (error) {
      console.error(error.message);
    }
  };

  useEffect(getPersons, []);

  const personList = persons.map(person => {
    if (
      person.name
        .toLowerCase()
        .trim()
        .includes(newFilter.toLowerCase().trim())
    ) {
      return (
        <div key={person.name}>
          <p>
            {person.name} {person.number}
          </p>
          <button onClick={() => removeUser(person)}>delete</button>
        </div>
      );
    }
  });

  const handleAddPerson = async event => {
    event.preventDefault();
    let match;
    let answer;

    const personObj = {
      name: newName,
      number: newNumber
    };

    persons.forEach(async person => {
      if (person.name.toLowerCase().trim() === newName.toLowerCase().trim()) {
        match = true;
        answer = window.confirm(
          `${newName} has already been added to the phonebook, would you like to replace the number?`
        );
        try {
          if (answer) {
            await phoneService.update(person.id, personObj);
            const allUsers = await phoneService.getAll();
            setPersons(allUsers);
            setNewName("");
            setNewNumber("");
          }
        } catch (error) {
          console.error(error.message);
        }
      }
    });

    if (!match) {
      try {
        const newUser = await phoneService.create(personObj);
        setPersons(persons.concat(newUser));
        setNewName("");
        setNewNumber("");
        setMessage(`Added ${newName}`);
        setTimeout(() => {
          setMessage(null);
        }, 5000);
      } catch (error) {
        console.error(error.message);
      }
    }
  };

  const handlePersonChange = event => {
    setNewName(event.target.value);
  };

  const handleNumberChange = event => {
    setNewNumber(event.target.value);
  };

  const handleFilterChange = event => {
    setNewFilter(event.target.value);
  };

  const removeUser = async person => {
    try {
      await phoneService.remove(person.id);
      const allUsers = await phoneService.getAll();
      setPersons(allUsers);
    } catch (error) {
      setMessage(
        `Information for ${person.name} has already been removed from the server.`
      );
      setTimeout(() => {
        setMessage(null);
      }, 5000);
    }
  };

  return (
    <div>
      <h1>Phonebook</h1>
      {!auth ? (
        <Togglable>
          <LoginForm />
        </Togglable>
      ) : null}
      <div>
        {auth ? (
          <div>
            <Notification message={message} />
            <div>
              filter shown with{" "}
              <input onChange={handleFilterChange} value={newFilter} />
            </div>
            <div>
              <h2>Add New Number</h2>
              <form onSubmit={handleAddPerson}>
                <div>
                  name: <input onChange={handlePersonChange} value={newName} />
                </div>
                <div>
                  number:{" "}
                  <input onChange={handleNumberChange} value={newNumber} />
                </div>
                <div>
                  <button type="submit">add</button>
                </div>
              </form>
            </div>
            <h2>Numbers</h2>
            <div>{personList}</div>
          </div>
        ) : null}
      </div>
    </div>
  );
};

export default PhonebookApp;
